"use client";

import { useEffect } from "react";

export function HideDevIndicator() {
  useEffect(() => {
    const hideIndicator = () => {
      // Скрытие всех возможных вариантов индикатора
      const selectors = [
        '.devtools-indicator',
        '[class*="devtools-indicator"]',
        '[class*="devtools"]',
        'button[aria-label*="Next"]',
        'a[aria-label*="Next"]',
        'div[aria-label*="Next"]',
        'button[title*="Next"]',
        'a[title*="Next"]',
      ];

      selectors.forEach(selector => {
        const elements = document.querySelectorAll(selector);
        elements.forEach(el => {
          (el as HTMLElement).style.display = 'none';
          (el as HTMLElement).style.visibility = 'hidden';
          (el as HTMLElement).style.opacity = '0';
          (el as HTMLElement).style.pointerEvents = 'none';
        });
      });

      // Скрытие элементов с fixed позицией внизу слева или справа
      const allElements = document.querySelectorAll('*');
      allElements.forEach(el => {
        const htmlEl = el as HTMLElement;
        const style = window.getComputedStyle(htmlEl);
        const rect = htmlEl.getBoundingClientRect();
        const text = htmlEl.textContent?.trim() || '';
        
        // Проверяем, находится ли элемент внизу (слева или справа) и содержит N или Next
        if (style.position === 'fixed' && 
            rect.bottom < 100 && 
            (rect.left < 100 || rect.right < 100) &&
            (text === 'N' || 
             text.includes('Next') ||
             htmlEl.querySelector('svg') || 
             htmlEl.getAttribute('aria-label')?.toLowerCase().includes('next') ||
             htmlEl.getAttribute('title')?.toLowerCase().includes('next') ||
             htmlEl.className?.toLowerCase().includes('dev') ||
             htmlEl.id?.toLowerCase().includes('dev'))) {
          htmlEl.style.display = 'none';
          htmlEl.style.visibility = 'hidden';
          htmlEl.style.opacity = '0';
          htmlEl.style.pointerEvents = 'none';
          htmlEl.remove(); // Полностью удаляем элемент из DOM
        }
      });
    };

    // Выполняем сразу
    hideIndicator();

    // Выполняем после небольшой задержки
    setTimeout(hideIndicator, 100);
    setTimeout(hideIndicator, 500);
    setTimeout(hideIndicator, 1000);

    // Наблюдаем за изменениями DOM
    const observer = new MutationObserver(() => {
      hideIndicator();
    });

    observer.observe(document.body, {
      childList: true,
      subtree: true,
      attributes: true,
      attributeFilter: ['class', 'style', 'aria-label'],
    });

    return () => observer.disconnect();
  }, []);

  return null;
}

